import React from 'react'
import InstagramCarousel from '../../containers/Instagram/InstagramCarousel';

export const InstagramFeed = (props) => (
    <div className='row mt-3'>
        <div className='container'>
            <div
                className='col-sm-12 py-2  text-center w-100'
                style={{
                backgroundColor: 'black'
            }}>
                <a className='font_2' href='https://www.instagram.com/cleantonn/'>
                    <div className='text-white'>
                          
                        <span className='color2 d-inline'>  instagram</span>
                    </div>
                </a>
            </div>
        </div>
        <InstagramCarousel hashTag={props.hashTag}/>
    </div>
)


