import React from 'react';
import ReactDom from 'react-dom'
//import {render} from 'react-snapshot'
import {createStore, applyMiddleware} from 'redux'
import Logger from 'redux-logger'
import {Provider} from 'react-redux'
import thunk from 'redux-thunk'
import {ConnectedRouter, routerMiddleware, connectRouter} from 'connected-react-router';

//import { createBrowserHistory} from 'history';
import {createHashHistory} from 'history';

import {rootReducer} from './reducers'

import routes from './routes/index'

import './fonts/fonts.css'

import registerServiceWorker from './registerServiceWorker';

//const history = createBrowserHistory({hashType:'hashbang'})
const history = createHashHistory({hashType: 'noslash'})
let middleware = [thunk, routerMiddleware(history)]
if (process.env.NODE_ENV !== 'production') {
    middleware = [
        ...middleware,
        Logger
    ]
}

const store = createStore(connectRouter(history)(rootReducer), window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__(), applyMiddleware(...middleware))

ReactDom.render(
    <Provider store={store}>
    <ConnectedRouter history={history}>

        {routes}

    </ConnectedRouter>
</Provider>, document.getElementById('root'));
registerServiceWorker();



