class InstagramCarousel extends Component {
    componentWillMount(props) {
        this
            .props
            .instaFetchData(this.props.hashTag)
    }

    render() {
        return (
            <div className='col-sm-12'>
                <Carousel photos={this.props.instaPhotos} id={this.props.id}/>
            </div>
        )

    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        instaPhotos: getInstaPhotos(state, ownProps.hashTag)
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        instaFetchData: (hashTag) => dispatch(instagramActions.fetchdata(hashTag))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(InstagramCarousel)



