import imutable from 'seamless-immutable'

const initialState = imutable([
    {
        main: ' ',
        but: '- ,         '
    }, {
        main: '   ',
        but: '- ,          ' +
                ' '
    }, {
        main: ' ',
        but: '-          '
    }, {
        main: '  ',
        but: '- ,   7      '
    }, {
        main: '    2000 ',
        but: '- ,       1000     '
    }, {
        main: ' ',
        but: '- ,          '
    }, {
        main: '   ',
        but: '-        '
    }
])

export const Advantages = (state = initialState, action) => {
    switch (action.type) {

        default:
            return state
    }
}

export function getAdvantages(state) {
    return state.Advantages
}


