import React from 'react'
import {Link} from 'react-router-dom'
import LogoImage from '../../images/logo.png'

const Logo = () => (

    <Link to='/' className='navbar-brand '>
        <img src={LogoImage} height='67' alt=""/>
    </Link>

)

export default Logo


