import React, {Component} from 'react'
import PropTypes from 'prop-types'
import './About.css'
export default class About extends Component {

    constructor(props) {
        super(props)
        console.log(props)
        switch (props.name) {
            case 'repair':
                this.rend = <AboutRepair/>
                break;
            case 'himchistka':
                this.rend = <AboutHimchistka/>
                break;
            case 'nano':
                this.rend = <AboutNano/>
                break;
            case 'polish':
                this.rend = <AboutPolish/>
                break;
            case 'wash':
                this.rend = <AboutWash/>
                break;
            case 'paint':
                this.rend = <AboutPaint/>
                break;

            default:
                this.rend = <div>
                     </div>
                break;
        }
    }

    render() {
        return (
            <div className='col-sm-8'>
                <div className='p-3 h-100 About font_7'>
                    {this.rend}
                </div>
            </div>
        )
    }
}

