import React, {Component} from 'react'
import './Footer.css'
import {withRouter} from 'react-router'
import {NavLink} from 'react-router-dom'
//import Instagram from './Instagram/Instagram';
 class Footer extends Component {
    render() {
        return (
            <footer className='container-fluid mt-1'>
            
                <div className='row'>
                    <div className='col-sm-12 ml-2'>
                        <p className='mt-2 '>:  
                            <NavLink to='/contact'  className='font_8'> -  3, </NavLink>
                        </p>
                        <em className='mt-2  '>  :
                            <a href='tel:+78412306806' className='color-grey  font_2'>+7(8412)<strong style={{ fontSize: '38px', color: 'black' }} >306-806</strong>
                            </a>
                        </em>
                        <p className='mt-4'>2018       </p> {/*2015*/}
                    </div>
                    <div className='col'>
                        {/*
                        <Instagram/>
                    */}
                    </div>
                </div>
            </footer>
        )
    }
}

export default withRouter(Footer)


