import React from 'react'
import CustomBody from './ServiceBody/CustomBody';
import ServiceBody from './ServiceBody/ServiceBody';

export const ServiceView = (props) => (
    <div className='mt-4'>
        {props.isCustom
            ? <CustomBody items={props.items}/>
            : <ServiceBody items={props.items}/>}

    </div>
)



