import React, {Component} from 'react'
import './Nomer.css'
import {platform} from '../../utils/platform';
import classnames from 'classnames'

class Nomer extends Component {
    render() {
        const isMobile = platform();
        return (
            <span className='navbar-nav pt-3  font_2'>
                <a
                    href='tel:+78412306806'
                    className={classnames('color-grey font_2', {'mx-auto': isMobile})}>
                    +7 (8412)
                    <strong
                        style={{
                        fontSize: '38px',
                        color: 'black'
                    }}>306-806</strong>
                </a>
            </span>
        )
    }
}

export default Nomer


