import Imutable from 'seamless-immutable'
import {FETCH_HASHTAGINFO_FAILED, FETCH_HASHTAGINFO_SUCCESS} from './actionTypes'

const initialState = Imutable({})

export const Instagram = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_HASHTAGINFO_SUCCESS:
            return state.set(action.hashtagName, action.hashtagData)
        case FETCH_HASHTAGINFO_FAILED:
            return state.merge({error: action.error})
        default:
            return state
    }
}

export function getInstaPhotos(state, hashtag) {
    return state.Instagram[hashtag]
}


